import React, { Component } from 'react';
import UrlAPI from '../utils/UrlApi';
import axios from 'axios';
import { ClipLoader } from "react-spinners";
import { css } from "@emotion/core";
import './style.css';

const override = css`
  display: block;
  margin: 0 auto;
  border-color: #11ABB0;
  padding-top: 20px;
`;

class FormContactUs extends Component {

    constructor() {
        super();
        this.state = {
            email: "",
            name: "",
            message: "",
            isLoading: false,
            errors: [],
            alert: null
        }
        this.handleChange = this.handleChange.bind(this);
        this.handlerChangeName = this.handlerChangeName.bind(this);
        this.handlerChangeText = this.handlerChangeText.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }

    validate(name, email, message) {
        // we are going to store errors for all fields
        // in a signle array
        const errors = [];

        if (name.length === 0) {
            errors.push("Name can't be empty");
        }

        if (email.length < 5) {
            errors.push("Email should be at least 5 charcters long");
        }
        if (email.split("").filter(x => x === "@").length !== 1) {
            errors.push("Email should contain a @");
        }
        if (email.indexOf(".") === -1) {
            errors.push("Email should contain at least one dot");
        }

        // if (message.length === 0) {
        //     errors.push("Message can't be empty");
        // }

        return errors;
    }

    onSubmit(event) {
        this.setState( { alert: null})
        const { name, email, password } = this.state;

        const errors = this.validate(name, email, password);
        if (errors.length > 0) {
            this.setState({ errors });
            return;
        }

        this.setState({ isLoading: true })

        const data = {
            email: this.state.email,
            name: this.state.name,
            message: this.state.message
        };
        console.log(data)
        axios({
            method: 'POST',
            url: `${UrlAPI.getUrl()}/sendEmail`,
            headers: {
                'Access-Control-Allow-Origin': 'https://lucasfonmiranda-email.herokuapp.com/',
                'Access-Control-Allow-Methods': 'POST',
                'Access-Control-Allow-Crecenterdentials': true,
                'Access-Control-Max-Age': '86400',
                'Access-Control-Allow-Headers':
                    'X-Requested-With, X-HTTP-Method-Override, Content-Type, Accept',
            },
            data: data
        })
            .then(res => {
                this.setState({ isLoading: false, alert: true });
            })
            .catch(err => {
                this.setState({ isLoading: false, alert: false });
            })
    };

    handleChange(event) {
        this.setState({ email: event.target.value });
    }

    handlerChangeName(event) {
        this.setState({ name: event.target.value })
    }

    handlerChangeText(event) {
        this.setState({ message: event.target.value })
    }

    renderButton() {
        if (this.state.isLoading === false) {
            return (
                <div style={{ paddingTop: 30, textAlign: "center" }}>
                    <button type="submit" onClick={() => this.onSubmit()}>Send</button>
                </div>

            )
        } else {
            return (
                <div style={{ justifyContent: "center", paddingTop: 30 }}>
                    <ClipLoader
                        css={override}
                        size={35}
                        //size={"150px"} this also works
                        color={"#123abc"}
                        loading={this.state.isLoading}
                    />
                </div>

            )

        }
    }

    render() {
        const { alert } = this.state;

        return (
            <form>
                <div>
                    <label>Name</label>
                    <input type="text" value={this.state.name} name="firstName" required onChange={this.handlerChangeName} />
                </div>
                <div>
                    <label>Email Address</label>
                    <input type="email" value={this.state.email} name="email" required onChange={this.handleChange} />
                </div>
                <div>
                    <label>Message</label>
                    <textarea type="message" value={this.state.message} name="message" required onChange={this.handlerChangeText} />
                </div>
                {alert === true &&< div className="alert success">Email send with success! Thanks</div>}
                {alert === false && <div className="alert">Something wrong! Try again.</div>}
                {this.renderButton()}
            </form>
        );
    }
}

export default FormContactUs;